import React, { useState } from 'react' 

import TextField from '@material-ui/core/TextField' 
import MenuItem from '@material-ui/core/MenuItem' 

const AccommodationType = (props) => {

    const [accommodationType, setAccommodationType] = useState(props.accommodationType || '') 

    const accommodationTypeOptions = [
        {
            value: "1st",
            label: "1st Class"
        },
        {
            value: "2nd",
            label: "2nd Class"
        },
        {
            value: "3rd",
            label: "3rd Class"
        }
    ]

    return (
        <div>
            <TextField
                select
                label="Accommodation Type"
                name="accommodation_type"
                value={accommodationType}
                onChange={(e) => setAccommodationType(e.target.value)}
                fullWidth
                required
            >
                {accommodationTypeOptions.map((option) => (
                    <MenuItem key={option.value} value={option.value}>
                        {option.label}
                    </MenuItem>
                ))}
            </TextField>
        </div>
    )
}

export default AccommodationType 