import React, { useState } from 'react'
import API from '../../Api'
import DateFns from '@date-io/date-fns'
import {
    Container,
    Row,
    Col,
    Card
} from 'reactstrap'
import { Paper } from '@material-ui/core'
import Alert from '@material-ui/lab/Alert'

import Logo from '../../assets/logo.png'
import HowToRegIcon from '@material-ui/icons/HowToReg'

import Step1 from '../Content/Steps/Step1'
import Step2 from '../Content/Steps/Step2'
import Step3 from '../Content/Steps/Step3'
import Step4 from '../Content/Steps/Step4'
import StepSuccess from '../Content/Steps/StepSuccess'

const Registration = (props) => {
    const dateFns = new DateFns()
    const [emailType, setEmailType] = useState("")
    const [email, setEmail] = useState("")
    const [isProcessing, setIsProcessing] = useState(false)
    const [isError, setIsError] = useState(false)
    const [errorMessage, setErrorMessage] = useState("")
    const [step, setStep] = useState('step1')
    const [profileId, setProfileId] = useState({})
    const [profile, setProfile] = useState([])
    const [personalDetails, setPersonalDetails] = useState({})
    const [uploaded_by, setUploaded_By] = useState('')

    const setUploadedBy = (x) => {
        setUploaded_By(x)
    }

    // This part of code is for navigation of steps
    const goto = (step, data = null) => {
        if (data !== null && step === "step3") {
            setProfileId({ "id": data.id })
            setProfile([data.first_name, data.last_name, data.middle_name, data.age, data.gender, data.mobile, data.address.zipcode, data.address.specific_address, data.person_type])
        }
        setIsError(false)
        setIsProcessing(false)
        setStep(step)
    }

    // This is use to validate email
    const emailValidationHandler = (e) => {
        e.preventDefault()
        setIsProcessing(true)
        var email = document.querySelector("input[name=email]").value
        if (email !== "") {
            setEmail(email)
            API.post('profile/verify', {
                "email": email
            }).then(result => {
                if (!result.data.status && result.status === 202) {
                    API.post('profile/email', {
                        "_email": email
                    }).then(result => {
                        if (result.status === 200) {
                            setEmailType("new")
                            setIsProcessing(false)
                            goto("step2")
                        } else {
                            setIsProcessing(false)
                            setErrorMessage("Invalid email address")
                            setIsError(true)
                        }
                    }).catch(e => {
                        setIsProcessing(false)
                        setErrorMessage("Something went wrong...")
                        setIsError(true)
                    })
                } else {
                    setEmailType("old")
                    setIsProcessing(false)
                    goto("step2")
                }
            }).catch(e => {
                setIsProcessing(false)
                setErrorMessage("Something went wrong...")
                setIsError(true)
            })
        } else {
            setIsProcessing(false)
            setErrorMessage("Please provide an email")
            setIsError(true)
        }

    }

    const codeValidationHandler = (e) => {
        e.preventDefault()
        let urlPath

        var code = document.querySelector("input[name=validationCode]").value
        if (code !== "") {

            if (emailType === "old") {
                urlPath = "profile/return-profile"
            } else {
                urlPath = "profile/check-code"
            }

            setIsProcessing(true)
            API.post(urlPath, {
                "_email": email,
                "_code": code
            }).then(result => {
                if (emailType === "old") {
                    goto("step3", result.data)
                } else {
                    if (result.status === 200) {
                        goto("step3")
                    }
                }
            }).catch(e => {
                setIsProcessing(false)
                setErrorMessage("Invalid Code")
                setIsError(true)
            })
        }
    }

    const personalDetailsHandler = (e) => {
        e.preventDefault()

        setIsProcessing(true)
        let formObj = document.querySelector("form[name=personalDetailsForm]")
        let result = Object.values(formObj.elements).reduce((obj, field) => {
            if (field.name !== "" && field.value !== "") {
                obj[field.name] = field.value
            }
            return obj
        }, {})

        if (Object.keys(result).length >= 1) {
            setPersonalDetails(result)
            goto("step4")
        } else {
            setErrorMessage("Please enter required fields")
            setIsError(true)
        }

        setIsProcessing(false)
    }

    const submitForm = (e) => {
        e.preventDefault()
        setIsProcessing(true)
        let formObj = document.querySelector("form[name=transportForm]")
        let result = Object.values(formObj.elements).reduce((obj, field) => {
            if (field.name !== "" && field.value !== "") {
                if (field.name === "arrival" || field.name === "departure") {
                    obj[field.name] = dateFns.format(new Date(field.value), "Y-M-dd H:m:s")
                } else {
                    obj[field.name] = field.value
                }
            }
            return obj
        }, {})

        if (Object.keys(result).length >= 1) {
            let uploadedBy = { "uploaded_by": uploaded_by }
            let profileData = { ...result, ...personalDetails, ...uploadedBy }
            let HttpReq
            if (emailType === "old") {
                HttpReq = API.put(`profile/register/${profileId.id}`, { ...profileData })
            } else {
                HttpReq = API.post(`profile/register`, { ...profileData })
            }


            HttpReq.then(result => {
                console.log(result)
                if (result.status === 200 && result.data.status) {
                    goto("stepSuccess", [email, profileData])
                } else {
                    setErrorMessage(result.data.message)
                    setIsError(true)
                }

                setIsProcessing(false)
            }).catch(e => {
                console.log(e);
                setErrorMessage("Transaction Failed")
                setIsError(true)
                setIsProcessing(false)
            })

        } else {
            setErrorMessage("Please enter required fields")
            setIsError(true)
            setIsProcessing(false)
        }
    }

    return (
        <Container className="mt-1 p-2">
            <Paper className="p-0 m-0" elevation={5}>
                <Row className="p-0 m-0">
                    <Col xs={3} className="text-center text-white myCardBoxHeight ">
                        <Row>
                            <Col md={12} className="myBgPrimary">
                                <a href="https://passengertracer.net/" rel="noopener noreferrer" className="btn m-0 p-0 mb-1 text-white">
                                    <img src={Logo} width="150" height="130" className="mt-2"/>                                    
                                </a>
                            </Col>
                        </Row>
                        <Row>
                            <Col md={12} className="myBgImage" style={{ height: "100vh" }}>
                                <h3 className="myTextSecondary mt-4">Passenger Module</h3>
                            </Col>
                        </Row>
                    </Col>
                    <Col xs={9} className="p-0 m-0 myCardBoxHeight">
                        <Card className="p-0 m-0 myCardBoxHeight" style={{ borderRadius: "0px", border: 'none' }}>
                            <Col xs={12} className="mt-4 myTextPrimary text-left">
                                <Row>
                                    <Col md={6}>
                                        <h2 style={{ fontSize: '2.1em' }} className="ml-2 mb-0"><HowToRegIcon style={{ fontSize: '1.5em' }} /> Registration Form</h2>
                                    </Col>
                                    <Col md={6}>
                                        {
                                            isError &&
                                            <Alert severity="error" className="mr-2">
                                                {errorMessage}
                                            </Alert>
                                        }
                                    </Col>
                                </Row>
                                <div className="mt-2 text-left">
                                    <Step1 visibility={step === "step1"} emailValidationHandler={emailValidationHandler} isProcessing={isProcessing} />
                                    <Step2 visibility={step === "step2"} goto={goto} codeValidationHandler={codeValidationHandler} isProcessing={isProcessing} />
                                    <Step3 visibility={step === "step3"} goto={goto} personalDetailsHandler={personalDetailsHandler} isProcessing={isProcessing} profile={profile} />
                                    <Step4 visibility={step === "step4"} goto={goto} submitForm={submitForm} isProcessing={isProcessing} setUploadedBy={setUploadedBy} />
                                    <StepSuccess visibility={step === "stepSuccess"} goto={goto} />
                                </div>
                            </Col>
                        </Card>
                    </Col>
                </Row>
            </Paper>
        </Container>
    )
}

export default Registration