import React, { useEffect, useState, useCallback } from 'react'
import API from '../../../../Api'

import Autocomplete from '@material-ui/lab/Autocomplete'
import TextField from '@material-ui/core/TextField'


const Provinces = (props) => {
    const [carriersList, setCarriersList] = useState([])
    const [selected, setSelected] = useState(null)

    const loadCarrier = useCallback(
        () => {
            API.get("profile/carriers").then(result => {
                if (result.status === 200) {
                    if (result.data !== null) {
                        result.data.sort()
                        let carriers = result.data.map(data => {
                            return {
                                value: data.id,
                                label: data.carrier_name
                            }
                        })
                        setCarriersList([{ "value": "", "label": "" }, ...carriers])
                    }
                }
            }).catch(e => {
                return null
            })

        },
        [],
    )

    const handleChange = (data) => {
        props.setUploadedBy(data.value)
        setSelected(data)
    }

    useEffect(() => {
        loadCarrier()
    }, [])

    return (
        <Autocomplete
            value={selected}
            onChange={(event, newValue) => {
                handleChange(newValue)
            }}
            options={carriersList}
            autoHighlight
            getOptionLabel={(option) => option.label}
            renderOption={(option) => (
                <React.Fragment>
                    {option.label}
                </React.Fragment>
            )}
            renderInput={(params) => (
                <TextField
                    {...params}
                    label="Choose a Shipping Line"
                    placeholder="Shipping Line"
                    required
                />
            )}
            fullWidth
            required
        />
    )
}

export default Provinces 