import React, { useState } from 'react' 

import TextField from '@material-ui/core/TextField' 
import MenuItem from '@material-ui/core/MenuItem' 

const TransportType = (props) => {

    const [transportType, setTransportType] = useState('2') 

    const setTransportValue = (value) => {
        props.setTransport(value);
        setTransportType(value);
    }

    const transportOptions = [
        // {
        //     value: "1",
        //     label: "Airplane"
        // },
        {
            value: "2",
            label: "Ship"
        },
        // {
        //     value: "3",
        //     label: "Land Vehicle"
        // }
    ] 

    return (
        <div>
            <TextField
                select 
                label="Select Transport Type"
                name="transport_type"
                value={transportType}
                onChange={(e) => setTransportValue(e.target.value)}
                fullWidth
                required
            >
                {transportOptions.map((option) => (
                    <MenuItem key={option.value} value={option.value}>
                        {option.label}
                    </MenuItem>
                ))}
            </TextField>
        </div>
    )
}

export default TransportType 