import React, { useState } from 'react'
import { Card, CardContent, TextField, Button, Typography } from '@material-ui/core'
import SkipNextIcon from '@material-ui/icons/SkipNext';
import SkipPreviousIcon from '@material-ui/icons/SkipPrevious'
import { Form, Spinner } from 'reactstrap'

const Step2 = (props) => {

    const [code, setCode] = useState();

    return (
        <div>
            {
                props.visibility &&
                <Card className="m-2 p-2">
                    <h3 className="myBgPrimary text-white p-2" style={{ fontSize: '1.5em' }}>Step 2: Code Validation</h3>
                    <CardContent>
                        <div className="m-0 p-0 pl-4">
                            <Typography>
                                <span style={{ fontSize: '1.3em' }}>We've sent you an email</span><br />
                                <small style={{ fontSize: "1.1em" }}>The system has sent an email validation code to your given email address. Copy the code given and paste it in the input field.</small>
                            </Typography>
                        </div>
                        <div className="m-0 p-0 pl-4 pr-4">
                            <Form onSubmit={(e) => props.codeValidationHandler(e)} autoComplete='off'>
                                <TextField className="mt-2" name="validationCode" defaultValue={code} onChange={(e) => setCode(e.target.value)} type="text" label="Validation Code" placeholder="Enter the code" fullWidth required />
                                <div className="text-right">
                                    {
                                        !props.isProcessing &&
                                        <Button type="button" onClick={() => props.goto("step1")} style={{ borderRadius: '0', minWidth: '150px' }} className="myBgPrimary text-white mt-2 mr-2" variant="contained" startIcon={<SkipPreviousIcon />}>
                                            <div className="pt-1"><span>Previous</span></div>
                                        </Button>
                                    }
                                    <Button type="submit" disabled={props.isProcessing} style={{ borderRadius: '0', minWidth: '150px' }} className="myBgPrimary text-white mt-2" variant="contained" endIcon={!props.isProcessing && <SkipNextIcon />}>
                                        {props.isProcessing ? <div className="text-center mt-1"><Spinner style={{ width: '1.5rem', height: '1.5rem' }} color="light" /></div> : <div className="pt-1"><span>Next</span></div>}
                                    </Button>
                                </div>
                            </Form>
                        </div>
                    </CardContent>
                </Card>
            }
        </div>
    )
}

export default Step2 