import React, { useState } from 'react'
import { Card, CardContent, TextField, Button, Typography } from '@material-ui/core'
import SkipNextIcon from '@material-ui/icons/SkipNext'
import { Form, Spinner } from 'reactstrap'

const Step1 = (props) => {

    const [email, setEmail] = useState('');

    return (
        <div>
            {
                props.visibility &&
                <Card className="m-2 p-2">
                    <h3 className="myBgPrimary text-white p-2" style={{ fontSize: '1.5em' }}>Step 1: Email Validation</h3>
                    <CardContent>
                        <div className="pl-4">
                            <Typography>
                                <span style={{ fontSize: '1.3em' }}>Your official email address</span><br />
                                <small style={{ fontSize: "1.1em" }}>This email address will receive notifications and account recovery information.</small>
                            </Typography>
                        </div>
                        <div className="m-0 p-0 pl-4 pr-4">
                            <Form onSubmit={(e) => props.emailValidationHandler(e)} autoComplete='off'>
                                <TextField className="mt-2" type="email" value={email} onChange={(e) => setEmail(e.target.value)} label="Email Address" placeholder="Enter your email address" fullWidth name="email" required />
                                <div className="text-right">
                                    <Button type="submit" disabled={props.isProcessing} style={{ borderRadius: '0', minWidth: '150px' }} className="myBgPrimary text-white mt-2" variant="contained" endIcon={!props.isProcessing && <SkipNextIcon /> }>
                                        {props.isProcessing ? <div className="text-center mt-1"><Spinner style={{ width: '1.5rem', height: '1.5rem' }} color="light" /></div> : <div className="pt-1"><span>Next</span></div>}
                                    </Button>
                                </div>
                            </Form>
                        </div>
                    </CardContent>
                </Card>
            }
        </div>
    )
}

export default Step1 