import React, { useState, useEffect } from 'react'
import { Card, CardContent, TextField, Button, Typography } from '@material-ui/core'
import { Row, Col, Form, Spinner } from 'reactstrap'

import SkipNextIcon from '@material-ui/icons/SkipNext'
import SkipPreviousIcon from '@material-ui/icons/SkipPrevious'

import GenderSelect from '../../Content/Forms/FormComponents/GenderSelect'
import TypeSelect from '../../Content/Forms/FormComponents/TypeSelect'

const Step3 = (props) => {

    const [person_type, setPersonType] = useState("")
    const [firstName, setFirstName] = useState("")
    const [lastName, setLastName] = useState("")
    const [middleName, setMiddleName] = useState("")
    const [age, setAge] = useState("")
    const [gender, setGender] = useState("")
    const [mobile, setMobile] = useState("")
    const [zipcode, setZipcode] = useState("")
    const [address, setAddress] = useState("")
    const [bodyTemperature, setBodyTemperature] = useState("")

    useEffect(() => {
        setPersonType(props.profile[8] || "")
        setFirstName(props.profile[0] || "")
        setLastName(props.profile[1] || "")
        setMiddleName(props.profile[2] || "")
        setAge(props.profile[3] || "")
        setGender(props.profile[4] || "")
        setMobile(props.profile[5] || "")
        setZipcode(props.profile[6] || "")
        setAddress(props.profile[7] || "")
    }, [props])

    return (
        <div>
            {
                props.visibility &&
                <Card className="m-2 p-2">
                    <h3 className="myBgPrimary text-white p-2" style={{ fontSize: '1.5em' }}>Step 3: Personal Details</h3>
                    <CardContent>
                        <Typography>
                            <span className="pl-4" style={{ fontSize: '1.3em' }}>Please provide the required personal data</span>
                        </Typography>
                        <div className="m-0 p-0 pl-4 pr-4">
                            <Form autoComplete="off" onSubmit={(e) => props.personalDetailsHandler(e)} name="personalDetailsForm">
                                <Row>
                                    <Col md={6}>
                                        <TypeSelect personType={person_type} />
                                        <TextField value={firstName} onChange={(e) => setFirstName(e.target.value)} label="First Name" placeholder="First Name" fullWidth className="mt-1" name="first_name" required />
                                        <TextField value={lastName} onChange={(e) => setLastName(e.target.value)} label="Last Name" placeholder="Last Name" fullWidth className="mt-1" name="last_name" required />
                                        <TextField value={middleName} onChange={(e) => setMiddleName(e.target.value)} label="Middle Name" placeholder="Middle Name" fullWidth className="mt-1" name="middle_name" />
                                        <TextField value={age} onChange={(e) => setAge(e.target.value)} label="Age" placeholder="Age" fullWidth className="mt-1" name="age" type="number" inputProps={{ min: "1" }} required />
                                    </Col>
                                    <Col md={6}>
                                        <GenderSelect gender={gender} />
                                        <TextField value={mobile} onChange={(e) => setMobile(e.target.value)} label="Mobile Number" placeholder="Mobile Number" fullWidth className="mt-1" name="mobile" required />
                                        <TextField value={zipcode} onChange={(e) => setZipcode(e.target.value)} label="Zipcode" placeholder="Zipcode" fullWidth className="mt-1" name="zipcode" required />
                                        <TextField value={address} onChange={(e) => setAddress(e.target.value)} label="Complete Address" placeholder="Complete Address" fullWidth className="mt-1" name="specific_address" required />
                                        <TextField
                                            type="number"
                                            value={bodyTemperature}
                                            name="temperature"
                                            label="Body Temperature"
                                            helperText="(Degree Celsius)"
                                            inputProps={{ min: "20", max: "50", step: "0.1" }}
                                            onChange={(e) => setBodyTemperature(e.target.value)}
                                            fullWidth
                                            required
                                        />
                                    </Col>
                                </Row>
                                <Row>
                                    <Col md={12} className="text-right mt-4 pt-4">
                                        <div className="text-right">
                                            {
                                                !props.isProcessing &&
                                                <Button type="button" onClick={() => props.goto("step2")} style={{ borderRadius: '0', minWidth: '150px' }} className="myBgPrimary text-white mt-2 mr-2" variant="contained" startIcon={<SkipPreviousIcon />}>
                                                    <div className="pt-1"><span>Previous</span></div>
                                                </Button>
                                            }
                                            <Button type="submit" disabled={props.isProcessing} style={{ borderRadius: '0', minWidth: '150px' }} className="myBgPrimary text-white mt-2" variant="contained" endIcon={!props.isProcessing && <SkipNextIcon />}>
                                                {props.isProcessing ? <div className="text-center mt-1"><Spinner style={{ width: '1.5rem', height: '1.5rem' }} color="light" /></div> : <div className="pt-1"><span>Next</span></div>}
                                            </Button>
                                        </div>
                                    </Col>
                                </Row>
                            </Form>
                        </div>
                    </CardContent>
                </Card>
            }
        </div>
    )
}

export default Step3 