import React, { useState } from 'react'
import { TextField } from '@material-ui/core'
import { Col, Form, Row } from 'reactstrap'
import DepartureDate from '../FormComponents/DepartureDate'
import ArrivalDate from '../FormComponents/ArrivalDate'

const CaapForm = (props) => {

    const [flight, setFlight] = useState('')
    const [airline, setAirline] = useState('')
    const [origin, setOrigin] = useState('')
    const [destination, setDestination] = useState('')

    return (
        <div className="mt-2">
            { props.visibility &&
                <div>
                    <Form autoComplete="off">
                        <Row>

                            <Col md={6}>
                                <div>
                                    <TextField
                                        name="flightNumber"
                                        label="Flight No."
                                        value={flight}
                                        onChange={(e) => setFlight(e.target.value)}
                                        fullWidth
                                        required
                                    />
                                    <TextField
                                        name="airline"
                                        label="Airline Company"
                                        value={airline}
                                        onChange={(e) => setAirline(e.target.value)}
                                        fullWidth
                                        required
                                    />
                                    <TextField
                                        name="origin"
                                        label="Origin"
                                        value={origin}
                                        onChange={(e) => setOrigin(e.target.value)}
                                        fullWidth
                                        required
                                    />
                                    <TextField
                                        name="Destination"
                                        label="Destination"
                                        value={destination}
                                        onChange={(e) => setDestination(e.target.value)}
                                        fullWidth
                                        required
                                    />
                                </div>
                            </Col>
                            <Col md={6}>
                                <DepartureDate />
                                <ArrivalDate />
                            </Col>
                        </Row>
                    </Form>
                </div>
            }
        </div>
    );
}

export default CaapForm;