import React, { useState } from 'react'

import { TextField } from '@material-ui/core'
import { Col, Row } from 'reactstrap'
import AccommodationType from '../FormComponents/AccommodationType'
import DepartureDate from '../FormComponents/DepartureDate'
import ArrivalDate from '../FormComponents/ArrivalDate'

const MarinaForm = (props) => {

    const [shipName, setShipName] = useState('')
    const [voyageNumber, setVoyageNumber] = useState('')
    const [seatNumber, setSeatNumber] = useState('')
    const [companion, setCompanion] = useState('')
    const [relative, setRelative] = useState('')
    const [relativeContact, setRelativeContact] = useState('')
    const [embarkation, setEmbarkation] = useState('')
    const [disembarkation, setDisembarkation] = useState('')
    const [route, setRoute] = useState('')
    const [parkingNumber, setParkingNumber] = useState('')
    const [travelHistory, setTravelHistory] = useState('')    

    return (
        <div className="mt-2">
            {props.visibility &&
                <div >
                    <Row>
                        <Col md={4}>
                            <input type="hidden" value="2" name="mode_of_travel_id" />
                            <TextField
                                value={shipName}
                                onChange={(e) => setShipName(e.target.value)}
                                name="ship_name"
                                label="Ship Name"
                                fullWidth
                                required
                            />
                            <TextField
                                value={relative}
                                onChange={(e) => setRelative(e.target.value)}
                                name="relative_name"
                                label="Nearest Relative"
                                fullWidth
                                required
                            />
                            <TextField
                                value={relativeContact}
                                onChange={(e) => setRelativeContact(e.target.value)}
                                name="relative_contact"
                                label="Relative Contact No."
                                fullWidth
                                required
                            />
                            <TextField
                                value={companion}
                                onChange={(e) => setCompanion(e.target.value)}
                                name="companion"
                                label="Child Companion"
                                helperText="Separate with comma(,)"
                                fullWidth
                                required
                            />
                        </Col>
                        <Col md={4}>
                            <TextField
                                value={voyageNumber}
                                onChange={(e) => setVoyageNumber(e.target.value)}
                                name="transport_no"
                                label="Voyage Number"
                                fullWidth
                                required
                            />
                            <AccommodationType />
                            <TextField
                                value={embarkation}
                                onChange={(e) => setEmbarkation(e.target.value)}
                                name="origin"
                                label="Port of Embarkation"
                                fullWidth
                                required
                            />
                            <TextField
                                value={disembarkation}
                                onChange={(e) => setDisembarkation(e.target.value)}
                                name="destination"
                                label="Port of Disembarkation"
                                fullWidth
                                required
                            />
                            <TextField
                                value={travelHistory}
                                onChange={(e) => setTravelHistory(e.target.value)}
                                name="previous_place_travel"
                                label="Place of Travel History"
                                helperText="( within 14 Days )"
                                fullWidth
                                required
                            />                            
                        </Col>
                        <Col md={4}>
                            <TextField
                                value={seatNumber}
                                onChange={(e) => setSeatNumber(e.target.value)}
                                name="seat_no"
                                label="Seat/Bunk Number"
                                fullWidth
                                required
                            />
                            <DepartureDate />
                            <ArrivalDate />
                            <TextField
                                value={route}
                                onChange={(e) => setRoute(e.target.value)}
                                name="route"
                                label="Voyage Route"
                                fullWidth
                                required
                            />
                            <TextField
                                value={parkingNumber}
                                onChange={(e) => setParkingNumber(e.target.value)}
                                name="parking_space_no"
                                label="Parking Space No."
                                fullWidth
                            />
                        </Col>
                    </Row>                    
                </div>
            }
        </div>
    );
}

export default MarinaForm;