import React, { useState } from 'react'
import { Card, CardContent, Typography, Button } from '@material-ui/core'
import { Row, Form, Col, Spinner } from 'reactstrap'

import HowToRegIcon from '@material-ui/icons/HowToReg'
import SkipPreviousIcon from '@material-ui/icons/SkipPrevious'

import TransportType from '../Forms/FormComponents/TransportType'
import CarrierType from '../Forms/FormComponents/CarrierType'

import CaapForm from '../Forms/caap/caapForm'
import LtfrbForm from '../Forms/ltfrb/ltfrbForm'
import MarinaForm from '../Forms/marina/marinaform'

const Step4 = (props) => {

    const [transport, setTransport] = useState("2")

    return (
        <div>
            {
                props.visibility &&
                <Card className="m-2 p-2">
                    <h3 className="myBgPrimary text-white p-2" style={{ fontSize: '1.5em' }}>Step 4: Transport Details</h3>
                    <CardContent>
                        <Typography>
                            <span className="pl-4" style={{ fontSize: '1.3em' }}>Please provide the required data</span>
                        </Typography>
                        <div className="m-0 p-0 pl-4 pr-4">
                            <Row>
                                <Form autoComplete="off" onSubmit={(e) => props.submitForm(e)} name="transportForm">
                                    <Col md={12}>
                                        <Row>
                                            <Col md={6}>
                                                <TransportType setTransport={setTransport} />
                                            </Col>
                                            <Col md={6}>
                                                <CarrierType setUploadedBy={props.setUploadedBy} />
                                            </Col>
                                        </Row>
                                    </Col>
                                    <Col md={12}>
                                        <CaapForm visibility={transport === "1"} submitForm={props.submitForm} goto={props.goto} />
                                        <MarinaForm visibility={transport === "2"} submitForm={props.submitForm} goto={props.goto} />
                                        <LtfrbForm visibility={transport === "3"} submitForm={props.submitForm} goto={props.goto} />
                                    </Col>
                                    <Row>
                                        <Col md={12} className="text-right mt-4 pt-4">
                                            {
                                                !props.isProcessing &&
                                                <Button type="button" onClick={() => props.goto("step3")} style={{ borderRadius: '0', minWidth: '150px' }} className="myBgPrimary text-white mt-2 mr-2" variant="contained" startIcon={<SkipPreviousIcon />}>
                                                    <div className="pt-1"><span>Previous</span></div>
                                                </Button>
                                            }
                                            <Button type="submit" disabled={props.isProcessing} style={{ borderRadius: '0', minWidth: '150px' }} className="myBgPrimary text-white mt-2" variant="contained" endIcon={!props.isProcessing && <HowToRegIcon />}>
                                                {props.isProcessing ? <div className="text-center mt-1"><Spinner style={{ width: '1.5rem', height: '1.5rem' }} color="light" /></div> : <div className="pt-1"><span>Submit Form</span></div>}
                                            </Button>
                                        </Col>
                                    </Row>
                                </Form>
                            </Row>
                        </div>
                    </CardContent>
                </Card>
            }
        </div>
    )
}

export default Step4 